import * as React from 'react';
import { MyLayout } from 'src/Layouts/MyLayout';

export const Resume = ({ match }: { match: any }) => {
    const { works, educations } = resumeHook();
    return (
        <MyLayout>
            <div className="bg-light-gray fl w-100" style={{ fontFamily: 'roboto' }}>
                <div className="mw8 w-100 center">
                    <div className="fl w-100">
                        <div className="fl w-100 tc pl3 mv3 pv3">
                            <span className="f3 b db navy">Work Experience</span>
                        </div>
                        <div className="fl w-100 pl3">
                            {
                                works.map(work => (
                                    <article className="relative timeline-item ph4 pv3 glow ">
                                        <div className="shadow-1 timeline-body">
                                            <h1 className="f6 shadow-1 bg-navy2 white  black-60 mv0 pv3 ph3 timeline-date">{work.time}</h1>
                                            <div className="ph3 pv3 bt b--black-10">
                                                <h3 className="f4 mv0 pb1 code">{work.title}</h3>
                                                <h3 className="f5 mv0 pb3 black-70 code">{work.companyName}</h3>
                                                <p className="f6 f5-ns mv0 lh-copy justify">
                                                    {work.details}
                                                </p>
                                            </div>
                                        </div>

                                    </article>
                                ))
                            }
                        </div>

                    </div>
                    <div className="fl w-100">
                        <div className="fl w-100 tc pl3 mv3 pv3">
                            <span className="f3 b db navy">Educational Qualification</span>
                        </div>
                        <div className="fl w-100 pl3">
                            {
                                educations.map(education => (
                                    <article className="relative timeline-item ph4 pv3 ">
                                        <div className="shadow-1 timeline-body">
                                            <h1 className="f6 shadow-1 bg-navy2 white  black-60 mv0 pv3 ph3 timeline-date">{education.time}</h1>
                                            <div className="ph3 pv2 bt b--black-10">
                                                <h3 className="f4 mv0 pb1 code">{education.title}</h3>
                                                <h3 className="f5 mv0 pb3 black-70 code">{education.institute}</h3>
                                                {/* <p className="f6 f5-ns mv0 lh-copy justify">
                                                {education.details}
                                            </p> */}
                                            </div>
                                        </div>
                                    </article>
                                ))
                            }
                        </div>

                    </div>
                </div>
            </div>
        </MyLayout>
    );
};

const resumeHook = () => {
    const [works] = React.useState([{
        time: 'December, 2018 - Present',
        companyName: 'INVARIANT TELECOM',
        title: 'Software Engineer',
        details: `I have already involved in two wonderful service like BUDDY and Wallet Service . Buddy is a ride sharing service
        and Wallet is a financial service. I contributed myself working with various technology like NodeJS, ExpressJS, MongoDB, VueJS . 
        Build Restfull api and Back office pannels`
    }, {
        time: 'March, 2018 - November, 2018',
        companyName: 'DOCTORKOI SMART',
        title: 'Full Stack Javascript Developer',
        details: `Doctorkoi SMART is prescription management software that helps Doctors to generate prescriptions 
        within just few clicks. It requires few seconds to prepare a full prescription by using SMART. Doctors can 
        customize prescription according to their needs. Medicines can be selected from 26,000+ registered drug lists. 
        So there is no chance to make any mistake`
    }, {
        time: 'January, 2017 - February, 2018',
        companyName: 'UY SYSTEM LTD',
        title: 'Web Developer',
        details: `Its an outsourcing software company. I have done multiple projects of outsourcing clients. Project of tourism management like
        tripzip.tours and Ecommerce Solution is done by me . NodeJS , Laravel , ExpressJS, Mysql , Mongodb are used for providing solutions`
    }]);

    const [educations] = React.useState([{
        time: '2013-2016',
        title: 'Bachelor of Computer Science and Engineering',
        institute: 'INTERNATIONAL UNIVERSITY OF BUSINESS AGRICULTURE AND TECHNOLOGY (IUBAT)',
    }, {
        time: '2009 - 2012',
        institute: 'DHAKA POLYTECHNIC INSTITUTE',
        title: 'Diploma in Engineering',
    }, {
        time: '2006-2008',
        institute: 'MIRPUR BANGLA HIGH SCHOOL',
        title: 'Secondary School Certificate (SSC)',
    }]);
    return { works, educations };
}


